.NavBar {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-right: 20px;

    ul {
        display: inline-flex;
    }
    
    button {    
        color: white;
        padding-top: 8px;
        padding-bottom: 8px;
        padding-left: 15px;
        padding-right: 15px;
        margin-right: 14px;
        border: none;
        background: black;
        font-family: Palatino;
        font-size: 30px;
        border-radius: 10%;
    }

    button:hover {
        background: gray;
        transition-timing-function: ease-out;
        transition-duration: 200ms;
        border-radius: 10%;
    }

}